"use client";

import { signOut, useSession } from "next-auth/react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import useSWR from "swr";

const fetcher = (url: string) =>
  fetch(url, { cache: "no-store" }).then((r) => r.json());

export default function TopNav() {
  const pathname = usePathname();
  const isAdminPath = pathname?.startsWith("/admin");
  const { data: session } = useSession();
  const role = (session?.user as any)?.role as "ADMIN" | "CLIENT" | undefined;

  // (선택) 채팅 배지 API 있으면 유지
  const { data } = useSWR<
    { ok: boolean; admin: number; user: number; total: number } | undefined
  >("/api/chat/unread", fetcher, {
    refreshInterval: 3000,
    revalidateOnFocus: true,
    refreshWhenHidden: false,
  });
  const count = isAdminPath ? (data?.admin ?? 0) : (data?.user ?? 0);

  return (
    <nav className="sticky top-0 z-40 bg-white/80 backdrop-blur border-b">
      <div className="max-w-6xl mx-auto flex items-center justify-between px-4 py-2">
        <Link href="/" className="font-semibold">권리금중개앱</Link>

        <div className="flex items-center gap-2">
          {role === "ADMIN" && (
            <Link href="/admin" className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50">
              관리자
            </Link>
          )}

          {session?.user && (
            <Link href="/listing/new"
                  className="rounded-lg bg-sky-600 text-white px-3 py-1.5 text-sm hover:bg-sky-700">
              내 매물 등록
            </Link>
          )}

          <Link
            href="/admin/chat"
            className="relative rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50"
            title="채팅"
          >
            채팅
            <span className="ml-2 inline-flex items-center justify-center min-w-[24px] h-6 px-2 text-xs rounded-full bg-sky-500 text-white">
              {count}
            </span>
          </Link>

          {session?.user ? (
            <>
              <span className="hidden sm:inline text-sm text-neutral-700 px-2">
                {session.user.name ?? session.user.email}
              </span>
              <button
                onClick={() => signOut({ callbackUrl: "/" })}
                className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50"
              >
                로그아웃
              </button>
            </>
          ) : (
            <>
              <Link href="/login" className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50">
                로그인
              </Link>
              <Link href="/register" className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50">
                회원가입
              </Link>
            </>
          )}
        </div>
      </div>
    </nav>
  );
}
